                    </template>
                    <template v-slot:tgslot-SendAccountCredentials="{data}">
                        <div class="col-lg-4 col-xl-3">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-9 col-sm-10 col-md-10 col-lg-10 col-xl-10">
                                        <a href="javascript:;" class="ancSendAccountCredentials" title="Send Account Credentials" v-on:click="sendAccountCredentials">
                                            <i class="fa fa-key text-dark" style="font-size: 38px;"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
                    <template v-slot:tgslot-ResetAndSentPassword="{data}">
                        <div class="col-lg-4 col-xl-3">
                            <div class="form-group">
                                <div class="row">
                                    <div class="col-9 col-sm-10 col-md-10 col-lg-10 col-xl-10">
                                        <a href="javascript:;" class="resetImg float-left" title="Reset and Send Password" v-on:click="resetAndSendPassword">
                                            <i class="fa fa-lock text-dark" style="font-size: 38px;"></i>
                                        </a>
