                                                            v-on:click="sendAccountCredentials" title=""
                                                            data-toggle-tooltip="tooltip"
                                                            data-original-title="Send Account Credentials">
                                                            <i class="fa fa-key text-dark" style="font-size: 38px;"></i>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>

                                        </template>


                                    </dynamic-form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> -->
                <div class="row flex-row-reverse mt-2">

                    <div class="col-lg-6 text-right">
                        <a class="btn btn-success mr-2" value="Save" title="Save" name="btnSave"
                            v-on:click="SubmitUser"><i class="fa fa-save mr-2" aria-hidden="true"></i> Save</a>
                        <a title="" class="btn btn-danger formbtn widthhalf" href="/User/Index"
                            data-toggle-tooltip="tooltip" data-original-title="Cancel"><i
                                class="fa fa-close mr-2"></i>Cancel</a>
                    </div>
                    <div class="col-lg-6">
                        <div class="py-2"><span class="mandatory">Fields marked with an asterisk (*) are
                                mandatory.</span></div>
                    </div>
                </div>
            </div>
        </div>
        <AddCompanySetup :recordId="recordId" :title="getTitle" v-if="ShowAllData" :addUserGuide="addUserGuide"
            v-on:close="ShowAllData = false; recordId = 0" v-bind:moduleName="getModuleName"
            v-bind:emitName="'addUserRecord'" v-bind:addApiUrl="getController" v-bind:subModule="getSubmoduleName" v-bind:callbackfunction="GetDropDownData" />

        <modalResetPassword v-if="isResetPasswordVisible" :UserResetPasswordData="UserResetPasswordData">
        </modalResetPassword>
        <modalAssignBonus v-if="ShowAssignBonusLeave" :userId="UserId"></modalAssignBonus>
    </div>
</template>

<script>
import DataService from '../../services/DataService';
import AddCompanySetup from '@/components/Modals/AddCompanySetup.vue';
import modalResetPassword from '../../components/Modals/modalUserResetPassword.vue';
import modalAssignBonus from '../../components/Modals/modalAssignBonus.vue';
import authService from "../../services/authservice";
export default {
    components: {
        AddCompanySetup,
        modalResetPassword,
        modalAssignBonus
    },
    props: {
        isEditUser: {
            type: String
        },
        UserId: {
            type: Number
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [],
            UserResetPasswordData: {},
            isResetPasswordVisible: false,
            FormSchemaAdvanceSettings: [],
            ShowAdvanceSetting: false,
            getTitle: '',
            getModuleName: '',
            getSubmoduleName: '',
            ShowAllData: false,
            getFormSchemaData: [],
            getFormSchemaAdvanceSettings: [],
            fileData: [],
            userTypeId: null,
            userName: '',
            userEmail: '',
            EncryptPassword:"",
            randSalt:"",
            GetTimeZoneId:"",
            addUserGuide:"",
            HourlyRate:"",
            ShiftId:null,
            DesignationId:null,
            DepartmentId:null,
            checkexistemail:false,
            ImageData:[],
            ShowAssignBonusLeave:false,
            enableSendPassword:"",
            decryptPassword :""
        }
    },
    async created() {
        var vm = this;
        await vm.GetHourlyRate();
        await vm.GetTimeZone();
        await vm.GetForm();
        vm.GetConfigurationValueByKey("ApplicationLevel","SendAccountDetailsToNewlyAddedUser");
        
       
    },
    methods: {
        AddNewTitlePopUp() {
            var vm = this;
            vm.getTitle = vm.$t('AddUserTitle');
            vm.getModuleName = 'Company';
            vm.getSubmoduleName = 'UserTitle';
            vm.getController = '/UserTitle/add';
            vm.addUserGuide = vm.$t('User_Guide_UserTitle_add');
            vm.ShowAllData = true;
        },
        AddADepartmentPopUp() {
            var vm = this;
            vm.getTitle = vm.$t('AddADepartment');
            vm.getModuleName = 'Company';
            vm.getSubmoduleName = 'Department';
            vm.getController = '/Department/Add';
            vm.addUserGuide = vm.$t('User_Guide_Department');
            vm.ShowAllData = true;
        },
        AddDesignationPopUp() {
            var vm = this;
            vm.getTitle = vm.$t('AddDesignation');
            vm.getModuleName = 'Company';
            vm.getSubmoduleName = 'Designation';
            vm.getController = '/Designation/Add';
            vm.addUserGuide = vm.$t('User_Guide_Designation');
            vm.ShowAllData = true;
        },
        AddShiftPopup() {
            var vm = this;
            vm.getTitle = vm.$t('Shift');
            vm.getModuleName = 'Company';
            vm.getSubmoduleName = 'Shift';
            vm.getController = '/Shift/Add';
            vm.addUserGuide = vm.$t('User_Guide_Shift');
            vm.ShowAllData = true;
        },
        async GetHourlyRate(moduleName,configKey){
            var vm = this;
            var moduleName = moduleName != undefined ? moduleName : "";
            var configKey = configKey != undefined ? configKey : 'UserBaseRate';
            var params = `moduleName=${moduleName}&configkey=${configKey}`;
            await DataService.GetHourlyRate(params).then(response=>{
                if(configKey == 'UserBaseRate'){
                    vm.HourlyRate = response.data[0].CONFIG_DATA_VALUE;
                }else if (configKey == 'Shift'){
                    vm.ShiftId = response.data[0].CONFIG_DATA_VALUE;
                }else if (configKey == 'Designation'){
                    vm.DesignationId = response.data[0].CONFIG_DATA_VALUE;
                }else if (configKey == 'Department'){
                    vm.DepartmentId = response.data[0].CONFIG_DATA_VALUE;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        ValidateConfirmPassword() {
            var vm = this;
            var getPasswordvalue = undefined;
            var getConfirmPasswordvalue = undefined;
            vm.FormSchema.forEach(item=>{
                item.Data.forEach(x=>{
                    if(x.name == "password"){
                        getPasswordvalue = x;
                    }else if (x.name == "confirm_password"){
                        getConfirmPasswordvalue = x;
                    }
                })
            })
            if (getPasswordvalue && getConfirmPasswordvalue != undefined) {
                if (getPasswordvalue.value == getConfirmPasswordvalue.value) {
                    getConfirmPasswordvalue.validationRules = {
                        "confirmed": false,
                        "required":true
                    }
                } else {
                    getConfirmPasswordvalue.validationRules = {
                        "confirmed": true,
                        "required":true
                    }
                }
            }
        },
        async GetForm() {

            var vm = this;
            vm.isLoading = true;
            let params = '';
            if (vm.UserId == undefined) {
                params = `recordId=0&moduleName=Company&subModuleCode=Cmp_User&otherData=&langCode=en&CustomLayoutId=`;
            } else {
                params = `recordId=${vm.UserId}&moduleName=Company&subModuleCode=Cmp_User&otherData=&langCode=en&CustomLayoutId=`;

            }
            DataService.GetDynamicForm(params).then(response => {
                if (response.data.data.length > 0) {
                    var getSchemaValue = vm.$refs.refAddUser.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    vm.FormSchema = getSchemaValue;
                    if (vm.UserId != undefined) {
                        vm.FormSchema[0].group_name = "";
                    }
                    vm.FormSchema.forEach(x=>{
                        x.Data.forEach((item,index) => {
                            if (item.name == "user_type_id") {
                                vm.userTypeId = item.value;
                                item.config.onChange= vm.GetUserTypeRole
                                if(vm.isEditUser == 'EditUser' && item.value != ""){

                                
                                  // vm.confirmR('Switching the user type will remove the already associated licenses. Do you want to proceed?', false, false, function (result) {

                                   //}); 
                                   
                                    

                                    vm.GetUserTypeRole("",item);
                                }
                            }
                            if (item.name == "first_name") {
                                vm.userName += " " + item.value;
                                item.validationRules = {
                                    "required":true,
                                    "max":100
                                }
                            }
                            if (item.name == "last_name") {
                                vm.userName += " " + item.value;
                                item.validationRules = {
                                    "required":true,
                                    "max":100
                                }
                            }
                            if (item.name == "email_id") {
                                vm.userEmail = item.value;
                                item.validationRules = {
                                    "required":true,
                                    "email": true,
                                    "max":200
                                }
                            }
                            if(item.name == "alias_name"){
                                item.validationRules = {
                                    "max":25
                                }
                            }
                            // if (item.name == 'gender') {
                            //     item.config.options = [{ name: 'Male', value: 'M' }, { name: 'Female', value: 'F' }];
                            // }
                            if (item.name == 'user_title_id') {
                                item.config.showAddIcon = true;
                                item.config.onAddButtonClick = vm.AddNewTitlePopUp;
                            }
                            if (vm.UserId != undefined) {
                                if (item.name == "password") {
                                    item.visibility = false
                                    item.config.onKeyUp=vm.ValidateConfirmPassword;
                                }
                                if (item.name == "confirm_password") {
                                    item.config.onKeyUp = vm.ValidateConfirmPassword;
                                    item.validationRules = {
                                        "confirmed": false
                                    }
                                    item.visibility = false
                                }
                            } 
                            else {
                                if (item.name == "password") {
                                    item.config.onKeyUp=vm.ValidateConfirmPassword;
                                    item.validationRules = {
                                        "required":true,
                                        "min":6,
                                        "max":50
                                    }
                                }
                                if (item.name == "confirm_password") {
                                    item.config.onKeyUp = vm.ValidateConfirmPassword;
                                    item.validationRules = {
                                        "confirmed": false,
                                        "required":true,
                                        "min":6,
                                        "max":50
                                    }
                                }
                            }
                            // if(item.name == "user_emp_id"){
                            //     item.validationRules = {
                            //         "required":true,
                            //         "max":25
                            //     }
                            // }
                            if(item.name == "timezone_id"){                                
                                if(item.value==''||item.value==null){
                                    item.value = vm.GetTimeZoneId;
                                }
                                // item.config.options.sort((a, b) => parseInt(a.value) - parseInt(b.value));                               
                            }
                            if (item.name == "is_enable_login") {
                                if(vm.isEditUser != 'EditUser')
                                {
                                    item.value = true
                                }
                                else{
                                    item.value = (item.value == 0 || item.value == "0") ? false : true;
                                }
                            }
                            if (item.name == "is_virtual") {
                                if (vm.isEditUser != 'EditUser')
                                {
                                    item.value = true
                                }
                                else{
                                    item.value = (item.value == 0 || item.value == "0") ? false : true;
                                }
                            }
                            if (item.name == "is_project_request_owner") {
                                if (vm.isEditUser != 'EditUser')
                                {
                                    item.value = true
                                }
                                else {
                                    item.value = (item.value == 0 || item.value == "0") ? false : true;
                                }
                            }
                            if(item.name == "hourly_rate"){
                                item.value = vm.HourlyRate;
                                item.validationRules = {
                                    "max":25
                                }
                            }
                            if (item.name == 'location_id' && item.value != '') {
                                var locationObj = x.Data.filter(x => x.name == 'shift_id')[0];
                                vm.GetDropDownData('USER_SHIFT_WITH_LOCATION', locationObj, item.value, 'EDIT');                                
                                vm.isLoading = false;
                            }
                            item.config.onChange = vm.onChangeLocation;                
                            if (item.name == 'shift_id') {
                                item.config.showAddIcon = true;
                                item.config.onAddButtonClick = vm.AddShiftPopup;
                            } else if (item.name == 'department_id') {
                                item.config.showAddIcon = true;
                                item.config.onAddButtonClick = vm.AddADepartmentPopUp;
                            } else if (item.name == 'designation_id') {
                                item.config.showAddIcon = true;
                                item.config.onAddButtonClick = vm.AddDesignationPopUp;
                            } else if (item.name == "policy_id") {
                                item.validationRules = {
                                    "required": false
                                }
                            } else if (item.name == 'date_format') {
                                item.config.options = [{ value: 'M/d/yyyy', name: 'M/D/YYYY' }, { value: 'dd/MM/yyyy', name: 'DD/MM/YYYY' }, { value: 'MM/dd/yyyy', name: 'MM/DD/YYYY' }]
                            }else if (item.name == 'assign_bonus' && vm.isEditUser == 'EditUser'){
                                let addAccountCredential = {
                                    astype: "SlotField",
                                    label: this.$t('AssignBonusLeaves'),
                                    name: "AssignBonusLeaves",
                                    value: "",
                                    visibility: true,
                                }
                                vm.FormSchema.forEach(outeritem=>{
                                    var checkassign = outeritem.Data.findIndex(x=> x.name == 'assign_bonus');
                                    if(checkassign > -1){outeritem.Data.splice(checkassign,1);}
                                })
                                x.Data.splice(index, 0, addAccountCredential);
                            }else if (item.name == 'reset_and_send_password' && vm.isEditUser == 'EditUser'){
                                let addAccountCredential = {
                                    astype: "SlotField",
                                    label: this.$t('ResetandSendPassword'),
                                    name: "ResetAndSentPassword",
                                    value: "",
                                    visibility: true,
                                }
                                vm.FormSchema.forEach(outeritem=>{
                                    var checkreset = outeritem.Data.findIndex(x=> x.name == 'reset_and_send_password');
                                    if(checkreset > -1){outeritem.Data.splice(checkreset,1);}
                                })
                                x.Data.splice(index, 0, addAccountCredential);
                            }else if (item.name == 'send_account_credential' && vm.isEditUser == 'EditUser'){
                                let addAccountCredential = {
                                    astype: "SlotField",
                                    label: this.$t('SendAccountCredentials'),
                                    name: "SendAccountCredentials",
                                    value: "",
                                    visibility: true,
                                }
                                vm.FormSchema.forEach(outeritem=>{
                                    var checkaccount = outeritem.Data.findIndex(x=> x.name == 'send_account_credential');
                                    if(checkaccount > -1){outeritem.Data.splice(checkaccount,1);}
                                })
                                x.Data.splice(index, 0, addAccountCredential);
                            }else if (item.name == 'is_project_request_owner' && vm.isEditUser == 'EditUser'){
                                item.label =  vm.$t('ProjectRequestOwner');
                                item.validationRules = {                                        
                                        "required":false,                                       
                                    }
                            }else if (item.name == 'avatar') {
                                item.config = {
                                    maxSize: 5,
                                    accept: [
                                        // {
                                        //     fileType: 'doc', // Document type
                                        //     iconClass: 'fa-file-word-o' // Icon class for documents
                                        // },
                                        // {
                                        //     fileType: 'docx', // Document type
                                        //     iconClass: 'fa-file-word-o' // Icon class for documents
                                        // },
                                        // {
                                        //     fileType: 'txt', // Text file type
                                        //     iconClass: 'fa-file-text-o' // Icon class for text files
                                        // },
                                        {
                                            fileType: 'jpg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'jpeg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'png', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'gif', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'bmp', // Image types
