        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                for (var i = 0; i < evt.length; i++) {
                    var f = evt[i];
                    var reader = new FileReader();
                    let fileObj = {};
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            var base64StringEncoded = window.btoa(binaryData);
                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);
                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;

                            fileObj.attachement_mime = attachemnetMime,
                            fileObj.fileName = fileName,
                            fileObj.base64String = base64StringEncoded,
                            fileObj.Size = f.size,
                            fileObj.contentType = f.type,
                            fileObj.ModuleName = 'Company',
                            fileObj.SubModuleCode = 'Cmp_User'
                            vm.fileData.push(fileObj);
                            const formData = new FormData();
                            formData.append('file', f);
                            formData.append('moduleName', "Company");
                            formData.append('subModuleName', "CMP_USER");
                            DataService.UploadImageFile(formData).then(response=>{
                                if(response.data.status == "1"){
                                    vm.ImageData.push({filePath : response.data.filePath});
                                }
                            }).catch(err=>{
                                console.log(err);
                            })
                        };
                    })(f);
                    reader.readAsBinaryString(f);
                }
            } catch (ex) {
                console.error("Error processing attachments:", ex);
            }
        },
        resetAndSendPassword() {
            var vm = this;
            var getschemaData = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
            var checkemail = getschemaData.find(x=> x.field_name == "email_id");
            vm.UserResetPasswordData = {
                user_id: vm.UserId,
                email_id: checkemail.field_value
            }
            vm.isResetPasswordVisible = true;
