        SubmitUser() {
            var vm = this;
            vm.$refs.refAddUser.validateForm.validate().then(async valid => {
                if (valid) {
                    vm.isLoading = true;
                    vm.getFormSchemaData = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    // // var getPassword = vm.getFormSchemaData.find(x=> x.field_name == "password");
                    // // await vm.GetEncryptPassword(getPassword.field_value);
                    // // getPassword.field_value = vm.EncryptPassword;

                    // // var getSalt = vm.getFormSchemaData.find(x=> x.field_name == "salt")
                    // // getSalt.field_value = vm.randSalt;
                    // vm.getFormSchemaAdvanceSettings = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchemaAdvanceSettings);
                    var checkemail = vm.getFormSchemaData.find(x=> x.field_name == "email_id")
                    if(checkemail.field_value != ""){
                        await vm.CheckEmailExist(checkemail.field_value);
                        if(vm.checkexistemail){
                            vm.FormSchema.forEach(item=>{
                                item.Data.forEach(x=>{
                                    if(x.name == "email_id"){
                                        x.value = "";
                                    }
                                    if(x.name == "password"){
                                        x.value = "";
                                    }
                                    if(x.name == "confirm_password"){
                                        x.value = "";
                                    }
                                })
                            })
                            vm.ShowAlert(vm.$t("EmailExists"), 'failure', true, vm.$t('Alert'));
                            vm.isLoading = false;
                            return;
                        }
                    }
                    var getFileList = vm.getFormSchemaData.find(x => x.field_name == "avatar");
                    if (getFileList != undefined && vm.fileData.length > 0) {
                        getFileList.field_value = vm.ImageData[0].filePath;
                    }
                    var getStatus = vm.getFormSchemaData.find(x => x.field_name == "status_id");
                    if (getStatus != undefined) {
                        getStatus.field_value = "1001";
                    }
                    var getrole_id = vm.getFormSchemaData.find(x => x.field_name == "role_id");
                    if (getrole_id != undefined && getrole_id.field_value === '') {
                        getrole_id.field_value = '0';
                    }
                    var checkexist = vm.getFormSchemaData.findIndex(x => x.field_name == "confirm_password");
                    if (checkexist != -1) {
                        vm.getFormSchemaData.splice(checkexist, 1);
                    }
                    var checkshift = vm.getFormSchemaData.find(x=> x.field_name == "shift_id");
                    if(checkshift != undefined && checkshift.field_value == ""){
                        await vm.GetHourlyRate("HR","Shift");
                        checkshift.field_value = vm.ShiftId == undefined ? "" : vm.ShiftId;
                    }
                    var checkdepartment = vm.getFormSchemaData.find(x=> x.field_name == "department_id");
                    if(checkdepartment != undefined && checkdepartment.field_value == ""){
                        await vm.GetHourlyRate("HR","Department");
                        checkdepartment.field_value = vm.DepartmentId == undefined ? "" : vm.DepartmentId;
                    }
                    var checkdesignation = vm.getFormSchemaData.find(x=> x.field_name == "designation_id");
                    if(checkdesignation != undefined && checkdesignation.field_value == ""){
                        await vm.GetHourlyRate("HR","Designation");
                        checkdesignation.field_value = vm.DesignationId == undefined ? "" : vm.DesignationId;
                    }
                    if (vm.UserId != undefined) {
                        vm.getFormSchemaData.forEach(item => {
                            if (item.field_name == "user_type_id") {
                                vm.userTypeId = '';
                                vm.userTypeId = item.field_value;
                            }
                            if (item.field_name == "first_name") {
                                vm.userName += " " + item.field_value;
                            }
                            if (item.field_name == "last_name") {
                                vm.userName += " " + item.field_value;
                            }
                        });
                        // vm.$emit("userTypeId", vm.userTypeId, vm.userName);
                    }
                    else{
                        var getPassword = vm.getFormSchemaData.find(x=> x.field_name == "password");
                         vm.decryptPassword= getPassword.field_value;
                        await vm.GetEncryptPassword(getPassword.field_value);
                        getPassword.field_value = vm.EncryptPassword;

                        var getSalt = vm.getFormSchemaData.find(x=> x.field_name == "salt")
                        if(getSalt != undefined){
                        getSalt.field_value = vm.randSalt;}
                    }
                    var tempjson = {
                        data: vm.getFormSchemaData,
                        moduleCode: "Company",
                        subModuleCode: "Cmp_User",
                        attachmentData: vm.fileData.length > 0 ? vm.fileData : null
                    }
                    var getJson = JSON.stringify(tempjson);
                    DataService.SaveUserForm(getJson).then(response => {
                        if (response.status == 200 && response.data[0].Status == "Success") {
                            if(vm.isEditUser == 'EditUser'){
                                vm.ShowAlert(vm.$t("UpdatedSuccess",[vm.$t('User')]), 'success', true, vm.$t('Alert'));
                                vm.$router.push("/User/Index");
                                authService.signIn(this.$route.path).then(() => {});
                            }else{
                            vm.ShowAlert(vm.$t("AddedSuccess",[vm.$t('User')]), 'success', true, vm.$t('Alert'));
                            if(vm.enableSendPassword=='yes')
                                {
                                    vm.UserSendMail(response.data[0].Value.MAIN_ID,checkemail.field_value,vm.decryptPassword)
                                }  
                            }
                            vm.$router.push("/User/Index");
                        } else {
                            vm.ShowAlert(vm.$t("AddedError",[vm.$t('User')]), 'failure', true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    }).catch(err => {
                        console.log(err);
                    })
                }
            })
        },
        UserSendMail(userId,emailId,decryptPassword) {
            debugger;
            var vm = this;
           // vm.isLoading = true;
           // var params = `userId=${ userId}&emailId=${emailId}&forcefully=true&password=${password};
            var params = `userId=${userId}&emailId=${emailId}&forcefully=true&password=${decryptPassword}`;
            DataService.SendMail(params).then(response => {
                if (parseInt(response.data.code) == 1) {
                    vm.ShowAlert(vm.$t("EmailSuccessfullySend"), 'success', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == 2) {
                    vm.ShowAlert(vm.$t("CompanyNotSMTPCredentials"), 'failure', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == -1) {
                    vm.ShowAlert(vm.$t(response.data.msg), 'failure', true, vm.$t('Alert'));
                }
                //vm.isLoading = false;
            });
        },
        GetConfigurationValueByKey(moduleName,configKey){
            var vm = this;
            var moduleName = moduleName != undefined ? moduleName : "";
            var configKey = configKey != undefined ? configKey : 'SendAccountDetailsToNewlyAddedUser';
            var params = `moduleName=${moduleName}&configkey=${configKey}`;
             DataService.GetConfigurationValueByKey(params).then(response=>{
                if(configKey == 'SendAccountDetailsToNewlyAddedUser'){
                    vm.enableSendPassword = response.data[0].CONFIG_DATA_VALUE;
                }
                
            }).catch(err=>{
                console.log(err);
            })
        },
        
        async GetEncryptPassword(password){
            var vm = this;
            var params = `password=${password}`;
            await DataService.GetEncryptPassword(params).then(response=>{
                if(response.data.length > 0){
                    vm.EncryptPassword = response.data[0].Password;
                    vm.randSalt = response.data[0].randSalt;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        GetUserTypeRole(x,y){
            debugger;
            var vm = this;
           let userTypeName = "";
            if(vm.isEditUser == 'EditUser' && x.type=='change'){
               vm.confirmR('Switching the user type will remove the already associated licenses. Do you want to proceed?', true, false, async function (result) {
                if (result === undefined) {
                    switch (y.value) {
              case "1":
                userTypeName = "CA";
                break;
              case "2":
                userTypeName = "PM";
                break;
              case "3":
                userTypeName = "NU";
                break;
              case "4":
                userTypeName = "CL";
                break;
              default:
                userTypeName = "";
            }
                await vm.DeAssociateLicense(userTypeName,'De-associate')
                
                var params = `userType=${y.value}`;
            DataService.GetUserTypeRole(params).then(response=>{
                //debugger;
                vm.FormSchema.forEach(x=>{
                    x.Data.forEach(y=>{
                        if(y.name == "role_id"){
                             vm.$refs.refAddUser.updateProperty("role_id", "config.options",[]);
                              vm.$refs.refAddUser.updateProperty("role_id", "value","");
                          }
                    })
                })
              vm.$refs.refAddUser.UpdateKeyValue();
                
            }).catch(err=>{
                console.log(err);
            })
                
                }
            });
            
           
            }
            else
            {

            var params = `userType=${y.value}`;
            DataService.GetUserTypeRole(params).then(response=>{
                vm.FormSchema.forEach(x=>{
                    x.Data.forEach(y=>{
                        if(y.name == "role_id"){
                            y.config.options = [];
                            if(response.data.DATA.length > 0){
                                response.data.DATA.forEach(item=>{
                                    y.config.options.push({value:item.role_id,name:item.role_name});
                                })
                            }
                        }
                    })
                })
                vm.$refs.refAddUser.UpdateKeyValue();
            }).catch(err=>{
                console.log(err);
            })
