         async  DeAssociateLicense(userType,DeAssociateValue) {

            try {
                var vm = this;
                vm.isLoading = true;
                var params = `moduleMasterID=0&userId=${vm.UserId}&userType=${userType}&actionType=${DeAssociateValue}&IsRemovedAll=true`;
                const response = await  DataService.GetAssociateDeAssociateLicense(params);
                if (response.data != null) {
                   if (DeAssociateValue == "De-associate") {
                        vm.ShowAlert(vm.$t("DeAssociateLicenseSuccess"), "success", vm.$t('Alert'));


                    }

                }

            } catch (error) {
                console.error('Error in AssociateDeAssociateLicense:', error);
