        sendAccountCredentials(){
            var vm = this;
            vm.isLoading = true;
            var getschemaData = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
            var checkemail = getschemaData.find(x=> x.field_name == "email_id");
            if(checkemail != undefined && checkemail.field_value != ""){
                var params = `userId=${vm.UserId}&emailId=${checkemail.field_value}&forcefully=true`;
                DataService.SendMail(params).then(response => {
                    if (parseInt(response.data.code) == 1) {
                        vm.ShowAlert(vm.$t("EmailSuccessfullySend"), 'success', true, vm.$t('Alert'));
                    } else if (parseInt(response.data.code) == 2) {
                        vm.ShowAlert(vm.$t("CompanyNotSMTPCredentials"), 'failure', true, vm.$t('Alert'));
                    } else if (parseInt(response.data.code) == -1) {
