        GetDropDownData: function (type, field, id) {
            var vm = this;
            if (id != '') {
                var url = `id=&moduleName=Company&type=${type}&search=&refId=${id}`;
            }else {
                var url = `id=&moduleName=Company&type=${type}&search=&refId=`;
            }            
            DataService.GetDDLData(url).then(response => {
                field.config.options = [];
                if (response.status == 200) {
                    if (type == "DEPARTMENT") {
                        field.value = '';
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                     else if (type == "USER_TITLE") {
                        field.value = '';
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                    else if (type == "USER_DESIGNATION") {
                        field.value = '';
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                    else if (type == "USER_SHIFT_WITH_LOCATION") {
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                    
                }
                vm.$refs.refAddUser.UpdateKeyValue();
            })
        }
