<template lang="en">
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 name="header" class="modal-title  pt-1">
                                {{ $t('Bonus') }}
                            </h4>
                            <span class="user-guide ">
                                <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false" style="color: white;" aria-controls="collapseExample">
                                    <em></em>User Guide</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('addBonusAssign')"></div>
                                    </div>
                                </div>
                            </span>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                    <loader :isVisible="isLoading"></loader>
            <div class="listing">
                <div class="table-responsive mb-2" id="divPopUp" style="max-height: 500px; overflow-y: auto">
                        <table class="table table-bordered dt-responsive nowrap mt-0 LeaveBonusAssign" style="min-width:1300px">
                            <thead class="thead-bg">
                                <tr>
                                    <th style="width:150px">
                                        Leave Type
                                    </th>
                                    <th style="width:222px" class="text-center">
                                        Leave(s) Allowed
                                    </th>
                                    <th style="width:210px" class="text-center">
                                        Opening Balance
                                    </th>
                                    <th style="width:270px" class="text-center">
                                        Pending for Approval
                                    </th>
                                    <th style="width:198px" class="text-center">
                                        Leave(s) Taken
                                    </th>
                                    <th style="width:162px" class="text-center">
                                        Bonus Leave
                                    </th>
                                    <th style="width:186px" class="text-center">
                                        Carry Forward
                                    </th>
                                    <th style="width:138px" class="text-center">
                                        Recurring
                                    </th>
                                </tr>
                            </thead>
                            <tbody v-for="item in BonusLeaveListing" :key="updatecomponent">
                                <tr class="parentRows"> 
                                    <td>{{item.LEAVE_TYPE_NAME}}</td>
                                    <td class="Leaves">
                                        {{item.LEAVES}}
                                    </td>
                                    <td class="OpeningBalance">
                                        {{item.OPENING_BALANCE}}
                                    </td>
                                    <td class="LeavePending">
                                        {{item.LEAVE_PENDING}}
                                    </td>
                                    <td class="Leavetaken">
                                        {{item.LEAVE_TAKEN}}
                                    </td>
                                    <td class="Leavetaken">
                                        <a href="javascript:;" class="MinusBonus float-left mt-2" title="less" v-on:click="AddAndMinusBonus(item,'MinusBonus')">◄</a>
                                        <input v-model="item.BONUS_LEAVE_BALANCE" type="text" pattern="\d*" class="LeaveBonus form-control float-left ml-1" style="width:60%" autocomplete="off" v-on:keyup="AddAndMinusBonus(item,'Keyup')">
                                        <a href="javascript:;" class="AddBonus float-right mt-2" title="more" v-on:click="AddAndMinusBonus(item,'AddBonus')">►</a>
                                    </td>
                                    <td class="text-center">
                                        <div class="custom-control custom-checkbox" v-if="item.LEAVES >= 0 && item.ISUNLIMITEDLEAVES == 0">
                                            <input :checked="item.IS_BONUS_CARRY_FORWARD == 1 ? 'checked' : ''" type="checkbox" :v-model="item.IS_BONUS_CARRY_FORWARD" name="IS_BONUS_CARRY_FORWARD" 
                                                class="isbonuscarryforward custom-control-input" :id="'chk_1' + item.LEAVE_TYPE_ID" style="display: none;" v-on:change="isCheckboxChecked($event,item,'IS_BONUS_CARRY_FORWARD')">
                                            <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_1' + item.LEAVE_TYPE_ID"></label>
                                        </div>
                                    </td>
                                    <td class="text-center">
                                        <div class="custom-control custom-checkbox" v-if="item.LEAVES >= 0 && item.ISUNLIMITEDLEAVES == 0">
                                            <input type="checkbox" :checked="item.IS_BONUS_RECUR == 1 ? 'checked' : ''" :v-model="item.IS_BONUS_RECUR" name="IS_BONUS_RECUR" 
                                                class="isbonusrecur custom-control-input" :id="'chk_2' + item.LEAVE_TYPE_ID" style="display: none;" v-on:change="isCheckboxChecked($event,item,'IS_BONUS_RECUR')">
                                            <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_2' + item.LEAVE_TYPE_ID"></label>
                                        </div>
                                    </td>
                                </tr>
                                <tr :class="item.BONUS_LEAVE_BALANCE == 0 ? 'commentRow d-none' : 'commentRow'" :id="'commentPerRow' + item.LEAVE_TYPE_ID">
                                    <td colspan="8" class="p-2" v-if="totalBonusLeave != SavedTotalBonusLeave && item.LEAVE_TYPE_ID == getAssignBonusId">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                            <div class="col-lg-12 p-0 mb-2">
                                                <div class="heading-border">
                                                    <h5 class="h5">
                                                        <span>Comment</span>
                                                    </h5>
                                                </div>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                <div class="form-group">
                                                    <textarea :id="'txtComment' + item.LEAVE_TYPE_ID" v-model="item.Comment" :name="'txtComment' + item.LEAVE_TYPE_ID"
                                                        class="textarea form-control" maxlength="200">
                                                    </textarea>
                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <td class="font-weight-bold">Total</td>
                                    <td id="thTotalLeaveAllowed" class="font-weight-bold">{{totalLeaves}}</td>
                                    <td id="thTotalOpening_Balance" class="font-weight-bold">{{totalOpeningBal}}</td>
                                    <td id="thTotalPendingforApproval" class="font-weight-bold">{{totalLeavePending}}</td>
                                    <td id="thTotalLeaveTaken" class="font-weight-bold">{{totalLeaveTaken}}</td>
                                    <td id="thTotalBonusLeaves" class="font-weight-bold arabicTR">{{totalBonusLeave}}</td>
                                    <td></td>
                                    <td></td>
                                </tr>
                            </tbody>
                    </table>
                </div>
                <div>
                <div class="col-md-12 p-0 mt-3">
                    <div class="theme-primary partition p-actions-expand tablehead isExtra">
                        <span class="p-name text-white" id="divModuleHeaderName">Assign/Deduct Bonus Leave(s) History</span>
                    </div>
                </div>
                <div class="comments container-fluid border pt-2 mb-2" style="max-height:300px; overflow-x:hidden; overflow-y:auto">     
                    <div id="accordion" class="w-100 activity-tab"> 
                        <div class="card" v-for="outeritem in BonusLeaveHistory">
                            <div class="card-header">
                                <a class="card-link" data-toggle="collapse" href="#collapseOne11">
                                    <div class="conversation-date heading-border"><span>{{ $options.filters.formatDate(outeritem.CreatedAt) }}</span></div>
                                </a>
                            </div>
                            <div id="collapseOne11" class="collapse show" data-parent="#accordion" v-for="bonusitem in outeritem.Items">
                                <div class="card-body">
                                    <div class="conversation-main">
                                        <div class="conversation-time"><span>{{ FormatDateToTime(bonusitem.CREATED_AT) }}</span></div>
                                                <div class="conversation-box-con">
                                                    <div class="conversation-box">
                                                        <div class="name-circle light-yellow">
                                                                <img class="img-circle avatar" loading="lazy" :src="'https://cogniterblob.blob.core.windows.net/'+GetUserInfo.ContainerName+'/Upload/UserProfileImage/'+bonusitem.AVATAR" width="51" height="51" alt="Image">
                                                        </div>
                                                        <div class="conversation-header addednote-ticket">
                                                            <span>
                                                                <span class="sender-name w-100"><strong>{{bonusitem.ADMIN_NAME}}</strong>
                                                                     {{'has' + bonusitem.DEVIATION > 0 ? 'Assigned' : 'Deducted' + bonusitem.DEVIATION + 'bonus leave(s)' + bonusitem.DEVIATION > 0 ? 'into' : 'from'}} 
                                                                     <strong>{{( bonusitem.LEAVE_TYPE_NAME )}}</strong>
                                                                </span>
                                                            </span>
                                                        </div>
                                                        <div class="conversation-content">
                                                            <p class="col-12 p-0 notesdata">
                                                                </p><h6 class="text-muted time"><i><b>{{ bonusitem.ADMIN_NAME }}</b>, Posted on: <span class="text-danger" title="9/11/2024 12:26:40 PM UTC">{{DateDifference(bonusitem.CREATED_AT)}}</span> </i></h6>
                                                            <p></p>
                                                            <span class="sender-department w-100">{{bonusitem.COMMENT}}</span>
                                                        </div>
                                                    </div>
                                                </div>                             
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

        </div>
            <div>
        </div>
                    <div class="row flex-row-reverse mb-2">
                        <div class="col-lg-12 text-right">
                            <a title="Click here to save record" href="javascript:;" class="submitbtn btn btn-success formbtn widthhalf" id="hlkSave" v-on:click="SaveBonusLeave">
                                <i class="fa fa-save pr-2 text-white"></i>Save
                            </a>
                            <a title="Cancel" href="javascript:;" class="submitbtn btn btn-danger formbtn widthhalf" id="hlkCancel" v-on:click="ClosePopUp">
                                <i class="fa fa-close pr-2 text-white"></i>Cancel
                            </a>
                        </div>
                    </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import moment from 'moment';
export default {
    props: {
        userId: {
            type: Object
        }
    },
    data() {
        return {
            isLoading: false,
            updatecomponent: 0,
            leaveAllowed: 0,
            totalLeaves: 0,
            totalOpeningBal: 0,
            totalLeavePending: 0,
            totalLeaveTaken: 0,
            totalBonusLeave: 0,
            SavedTotalBonusLeave: 0,
            SavedAddBonusArray: [],
            BonusLeaveListing: [],
            BonusLeaveHistory: [],
            getAssignBonusId:0
        }
    },
    async created() {
        var vm = this;
        await vm.LeaveBonusList();
        await vm.GetLeaveBonusHistory();
    },
    methods: {
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseAssignBonusLeave();
        },
        isCheckboxChecked(evt, item, name) {
            if (name == 'IS_BONUS_CARRY_FORWARD') {
                if (evt.target.checked) {
                    item.IS_BONUS_CARRY_FORWARD = 1;
                    vm.updatecomponent += 1;
                } else {
                    item.IS_BONUS_CARRY_FORWARD = 0;
                    vm.updatecomponent += 1;
                }
            } else {
                if (evt.target.checked) {
                    item.IS_BONUS_RECUR = 1;
                    vm.updatecomponent += 1;
                } else {
                    item.IS_BONUS_RECUR = 0;
                    vm.updatecomponent += 1;
                }
            }
        },
        async LeaveBonusList() {
            var vm = this;
            var params = `userId=${vm.userId}`;
            vm.isLoading = true;
            await DataService.LeaveBonus(params).then(response => {
                if (response.data.length > 0) {
                    vm.BonusLeaveListing = response.data;
                    vm.BonusLeaveListing.forEach(item => {
                        vm.totalLeaves = vm.totalLeaves + item.LEAVES;
                        vm.totalOpeningBal = vm.totalOpeningBal + item.OPENING_BALANCE;
                        vm.totalLeavePending = vm.totalLeavePending + item.LEAVE_PENDING;
                        vm.totalLeaveTaken = vm.totalLeaveTaken + item.LEAVE_TAKEN;
                        vm.totalBonusLeave = vm.totalBonusLeave + item.BONUS_LEAVE_BALANCE;
                        vm.SavedTotalBonusLeave = vm.totalBonusLeave;
                    })
                }
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
        AddAndMinusBonus(item, name) {
            var vm = this;
            var MaxBonus = 365;
            if (name == 'AddBonus') {
                item.BONUS_LEAVE_BALANCE = item.BONUS_LEAVE_BALANCE + 1;
                vm.SavedAddBonusArray.push(item.LEAVE_TYPE_ID);
                vm.totalBonusLeave = vm.totalBonusLeave + 1;
                vm.getAssignBonusId = item.LEAVE_TYPE_ID;
                vm.updatecomponent += 1;
            } else if (name == 'Keyup') {
                vm.updatecomponent += 1;
            } else {
                item.BONUS_LEAVE_BALANCE = item.BONUS_LEAVE_BALANCE - 1;
                var checkexist = vm.SavedAddBonusArray.findIndex(x => x == item.LEAVE_TYPE_ID);
                if (checkexist > -1) {
                    vm.SavedAddBonusArray.splice(checkexist, 1);
                }
                vm.totalBonusLeave = vm.totalBonusLeave - 1;
                vm.getAssignBonusId = item.LEAVE_TYPE_ID;
                vm.updatecomponent += 1;
            }
            vm.leaveAllowed = (item.LEAVES + item.OPENING_BALANCE) - item.LEAVE_TAKEN;
            var bonusVal = item.BONUS_LEAVE_BALANCE;
            if (isNaN(bonusVal)) {
                item.BONUS_LEAVE_BALANCE = 0;
                return true;
            }
            if (bonusVal < 0) {
                if ((-1 * bonusVal) <= vm.leaveAllowed) {
                    return true;
                }
                else {
                    vm.ShowAlert(vm.$t("BonusLeaveDeductMessage", [vm.leaveAllowed]), 'warning', true, vm.$t('Alert'));
                    item.BONUS_LEAVE_BALANCE = 0;
                    return false;
                }
            }
            else {
                if (bonusVal <= MaxBonus) {
                    return true;
                }
                else {
                    vm.ShowAlert(vm.$t("BonusLeaveMaxMessage", [MaxBonus]), 'failure', true, vm.$t('Alert'));
                    return false;
                }
            }
        },
        SaveBonusLeave() {
            var vm = this;
            var val = '';
            var bonuscarryforward = 0;
            var bonusrecur = 0;
            if (vm.SavedTotalBonusLeave != vm.totalBonusLeave) {
                for (const item of vm.BonusLeaveListing) {
                    bonuscarryforward = item.IS_BONUS_CARRY_FORWARD;
                    bonusrecur = item.IS_BONUS_RECUR;

                    if (item.BONUS_LEAVE_BALANCE == '' || isNaN(item.BONUS_LEAVE_BALANCE)) {
                        item.BONUS_LEAVE_BALANCE = 0;
                    }
                    var getval = vm.SavedAddBonusArray.find(x => x == item.LEAVE_TYPE_ID);
                    if (getval != undefined) {
                        if (item.Comment && item.Comment.trim() !== '') {
                            if (val.length > 0) {
                                val += "~";
                            }
                            val += item.LEAVE_TYPE_ID + "^" + item.BONUS_LEAVE_BALANCE + "^" + bonuscarryforward + "^" + bonusrecur + "^" + item.BONUS_LEAVE_BALANCE + "^" + item.Comment;
                        } else {
                            vm.ShowAlert(vm.$t("PleaseEnterAComment"), 'failure', true, vm.$t('Alert'));
                            return false;
                        }
                    }
                }
                var param = `bonus=${val}&userId=${vm.userId}`;
                DataService.SaveBonusLeave(param).then(response => {
                    if (response.data.status == 0) {
                        vm.ShowAlert(vm.$t("AddedSuccessBonus"), 'success', true, vm.$t('Alert'));
                    } else if (response.data.status == -1) {
                        vm.ShowAlert(vm.$t("ErrorBonusFailure"), 'failure', true, vm.$t('Alert'));
                    }
                    vm.ClosePopUp();
                }).catch(err => {
                    console.log(err);
                })
            } else {
                vm.ShowAlert(vm.$t("PleaseChangeBonus"), 'failure', true, vm.$t('Alert'));
            }
        },
        async GetLeaveBonusHistory() {
            var vm = this;
            var params = `userId=${vm.userId}`;
            await DataService.GetLeaveBonusHistory(params).then(response => {
                if (response.data.length > 0) {
                    vm.BonusLeaveHistory = response.data;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        FormatDateToTime(Getdate) {
            return moment(Getdate).format('LT');
        },
        DateDifference(ActualDate) {
            var vm = this;
            var GetCurrentDate = new Date();
            var GetValueInSeconds = Math.ceil(Math.floor(GetCurrentDate.getTime() - new Date(ActualDate).getTime()) / 1000);
            // TO Get Hours
            var hours = Math.floor(GetValueInSeconds / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = GetValueInSeconds % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            var days = Math.floor(GetValueInSeconds / (3600 * 24));
            if (GetValueInSeconds < 60) {
                return vm.$t('fewsecondsago');
            } else if (GetValueInSeconds < 120) {
                return vm.$t('minutesago');
            } else if (GetValueInSeconds < 2700) {
                return minutes + ' ' + vm.$t('minutesago');
            } else if (GetValueInSeconds < 5400) {
                return vm.$t('an') + ' ' + vm.$t('hourago');
            } else if (GetValueInSeconds < 86400) {
                return hours + ' ' + vm.$t('hourago');
            } else if (GetValueInSeconds < 172800) {
                return vm.$t('Yesterday');
            } else if (GetValueInSeconds < 2592000) {
                return days + ' ' + vm.$t('daysago');
            } else if (GetValueInSeconds < 31104000) {
                var months = Math.floor(days / 30);
                return months <= 1 ? '1 ' + vm.$t('monthsago') : months + ' ' + vm.$t('monthsago')
            } else {
                var year = Math.floor(days / 365);
                return year <= 1 ? '1 ' + vm.$t('yearsago') : year + ' ' + vm.$t('yearsago');
            }
        }
    }
}
</script>